  display: flex;
  gap: 1rem;
}

nav button {
  background: #624cab;
  color: #fff;
  border: none;
}

.important-icon path {
  fill: #fff;
  color: #fff;
}

.feather-icon {
  width: 50px;
  height: 50px;
}

.project-container {
}

.todo-container {
  display: flex;
}

.hide {
  display: none;
}

.important {
  color: #d81159;
}

.chevron-icon-container {
  transition: transform 0.3s ease; /* smooth transition for the flip in class .flip-icon */
}

.flip-icon {
  transform: scaleY(-1); /* vertical flip */
}

.active-btn-container.active {
  outline: 2px solid #111;
}
.completed {
  color: #c1cefe;
  text-decoration: line-through;
}
